@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@100&display=swap');


.time-adjust{
    width: 150px;
    height: 100px;
    
    padding-left: auto;

}

.timer {


    width: 600px;
    height: 300px;
    position: absolute;
    top:0;
    bottom: 0;
    left: 0;
    right: 0;
    
    margin: auto;
    margin-top: 420px;
    padding-left: 90px;
    
}

span{
    font-size: 90px;
    font-family: 'Roboto Mono', monospace;
    color: rgb(228, 225, 225);
}

p{
    color: #AA2B1D;
    font-family: 'Roboto Mono', monospace;
    font-weight: bold;
}